<template>
  <el-container>
    <el-main>
      <el-row>
        <el-col :span="6" :offset="9">
          <transition name="el-zoom-in-center">
            <el-card>
              <div class="ta-c">
                <img src="../assets/logo.jpg" class="image">
              </div>
              <el-form ref="form" :model="form" label-width="80px" label-position="top">
                <el-form-item label="用户名">
                  <el-input v-model="form.loginAccount"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                  <el-input v-model="form.password" type="password"></el-input>
                </el-form-item>
                <el-form-item class="ta-c">
                  <el-button type="primary" @click="onSubmit" :loading="loading">登录</el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </transition>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          loginAccount: '',
          password: ''
        },
        loading: false // 登录按钮的Loading状态，防止重复点击
      };
    },
    methods: {
      onSubmit() {
        this.loading = true;
        // 提交登录action
        this.$store.dispatch('auth/login', this.form)
        // 提交获取权限与角色action
        .then(({loginAccount}) => this.$store.dispatch('auth/getRolesAndResources', {loginAccount}))
        // 提交动态路由设置action
        .then(() => {
          this.loading = false;
          this.$router.replace('/index');
        })
        .catch((err) => {
          this.loading = false;
          console.error(err);
        });
      }
    }
  };

</script>

<style scoped>
  .ta-c {
    text-align: center;
  }
</style>

